.container {
    max-width: 1400px;
    padding: 0 40px;
    margin: 0 auto;
    box-sizing: border-box;
}

@media screen and (max-width: 992px) {
    .container {
        padding: 0 30px;
    }
}

@media screen and (max-width: 768px) {
    .container {
        padding: 0 20px;
    }
}
